.media {
  height: inherit;
  max-width: 100%;
  min-height: inherit;
  min-width: inherit;
  padding: 18px 24px 0 24px;
  position: relative;
  text-align: left;
  width: 9999px;

  @raw  {
    composes: flex-row from '../flex.styles';
  }

  &:empty {
    display: none;
  }
}

.media + .meta {
  display: none;
}

.description {
  padding: 12px 0;
  text-align: left;
  width: 100%;
}

.text,
.name {
  color: rgba(0, 0, 0, .8);
  font-size: 14px;
}

.text {
  color: rgba(0, 0, 0, .6);
}

.name {
  font-weight: bold;
  margin-right: 6px;
}

.name:empty {
  display: none;
}



