.error {
  max-width: 680px;
  padding: 16px 12px;
}

.image {
  background: url('../../assets/images/hovercards-error.png');
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  width: 80px;
  height: 80px;
  margin: auto;
  margin-bottom: 8px;
  margin-right: 12px;

  &:hover {
    animation-duration: 100ms;
    animation-iteration-count: infinite;
    animation-name: shake;
    animation-timing-function: ease-in-out;
  }
}

.description,
.name,
.text {
  display: block;
  text-align: center;
}

.cta {
  background-color: #185688;
  background-position: 10px 50%;
  background-repeat: no-repeat;
  background-size: 16px;
  border-radius: 15px;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  margin-top: 10px;
  padding: 6px 8px 6px 30px;

  &.imgur {
    background-image: url('../../assets/images/imgur.svg');
  }

  &.instagram {
    background-image: url('../../assets/images/instagram.svg');
  }

  &.reddit {
    background-image: url('../../assets/images/reddit.svg');
  }

  &.soundcloud {
    background-image: url('../../assets/images/soundcloud.svg');
  }

  &.twitter {
    background-image: url('../../assets/images/twitter.svg');
  }

  &.youtube {
    background-image: url('../../assets/images/youtube.svg');
  }
}

@raw  {
  @keyframes shake {
    2% {
      transform: translate(-0.5px, 0.5px) rotate(0.5deg);
    }

    4% {
      transform: translate(-0.5px, -0.5px) rotate(0.5deg);
    }

    6% {
      transform: translate(2.5px, 2.5px) rotate(-0.5deg);
    }

    8% {
      transform: translate(2.5px, 2.5px) rotate(-0.5deg);
    }

    10% {
      transform: translate(2.5px, -1.5px) rotate(1.5deg);
    }

    12% {
      transform: translate(-1.5px, 2.5px) rotate(-0.5deg);
    }

    14% {
      transform: translate(0.5px, 2.5px) rotate(1.5deg);
    }

    16% {
      transform: translate(-1.5px, -1.5px) rotate(0.5deg);
    }

    18% {
      transform: translate(0.5px, -1.5px) rotate(0.5deg);
    }

    20% {
      transform: translate(2.5px, -1.5px) rotate(0.5deg);
    }

    22% {
      transform: translate(0.5px, 0.5px) rotate(-0.5deg);
    }

    24% {
      transform: translate(1.5px, 1.5px) rotate(-0.5deg);
    }

    26% {
      transform: translate(1.5px, -0.5px) rotate(0.5deg);
    }

    28% {
      transform: translate(2.5px, -0.5px) rotate(0.5deg);
    }

    30% {
      transform: translate(1.5px, 2.5px) rotate(-0.5deg);
    }

    32% {
      transform: translate(1.5px, 1.5px) rotate(1.5deg);
    }

    34% {
      transform: translate(-1.5px, 0.5px) rotate(-0.5deg);
    }

    36% {
      transform: translate(2.5px, 2.5px) rotate(-0.5deg);
    }

    38% {
      transform: translate(-1.5px, -1.5px) rotate(-0.5deg);
    }

    40% {
      transform: translate(1.5px, -0.5px) rotate(1.5deg);
    }

    42% {
      transform: translate(0.5px, -1.5px) rotate(1.5deg);
    }

    44% {
      transform: translate(-1.5px, -1.5px) rotate(1.5deg);
    }

    46% {
      transform: translate(2.5px, 2.5px) rotate(1.5deg);
    }

    48% {
      transform: translate(0.5px, 0.5px) rotate(0.5deg);
    }

    50% {
      transform: translate(1.5px, -1.5px) rotate(-0.5deg);
    }

    52% {
      transform: translate(-1.5px, 2.5px) rotate(-0.5deg);
    }

    54% {
      transform: translate(-1.5px, -0.5px) rotate(-0.5deg);
    }

    56% {
      transform: translate(1.5px, 0.5px) rotate(0.5deg);
    }

    58% {
      transform: translate(0.5px, -0.5px) rotate(0.5deg);
    }

    60% {
      transform: translate(0.5px, 1.5px) rotate(0.5deg);
    }

    62% {
      transform: translate(2.5px, -0.5px) rotate(0.5deg);
    }

    64% {
      transform: translate(1.5px, -0.5px) rotate(0.5deg);
    }

    66% {
      transform: translate(0.5px, -1.5px) rotate(-0.5deg);
    }

    68% {
      transform: translate(-0.5px, 0.5px) rotate(1.5deg);
    }

    70% {
      transform: translate(0.5px, 1.5px) rotate(0.5deg);
    }

    72% {
      transform: translate(2.5px, -0.5px) rotate(-0.5deg);
    }

    74% {
      transform: translate(0.5px, 1.5px) rotate(-0.5deg);
    }

    76% {
      transform: translate(-0.5px, 1.5px) rotate(0.5deg);
    }

    78% {
      transform: translate(0.5px, 0.5px) rotate(-0.5deg);
    }

    80% {
      transform: translate(2.5px, -1.5px) rotate(0.5deg);
    }

    82% {
      transform: translate(0.5px, 1.5px) rotate(0.5deg);
    }

    84% {
      transform: translate(-1.5px, -1.5px) rotate(-0.5deg);
    }

    86% {
      transform: translate(0.5px, 2.5px) rotate(1.5deg);
    }

    88% {
      transform: translate(0.5px, 2.5px) rotate(-0.5deg);
    }

    90% {
      transform: translate(2.5px, 1.5px) rotate(1.5deg);
    }

    92% {
      transform: translate(-1.5px, -1.5px) rotate(0.5deg);
    }

    94% {
      transform: translate(-0.5px, -1.5px) rotate(-0.5deg);
    }

    96% {
      transform: translate(-0.5px, -1.5px) rotate(0.5deg);
    }

    98% {
      transform: translate(-0.5px, 1.5px) rotate(0.5deg);
    }

    0%,
    100% {
      transform: translate(0, 0) rotate(0);
    }
  }
}



