* {
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, BlinkMacSystemFont, 'Roboto', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-smooth: always;
}

*:before,
*:after {
  display: none;
}

*,
*:before,
*:after {
  all: initial; /* no important */
  box-sizing: border-box;
}

div {
  display: block;
}

